import Vue from 'vue'

const tel = /^1[3-9]\d{9}$/;

// 昵称过长
Vue.filter('longtext', function (value, maxLength = 8) {
  if (!value) return ''
  if (tel.test(value)) {
    return value
  }
  value = value.toString()
  return value.length > maxLength ? `${value.slice(0,2)}**${value.slice(-1)}` : value
})