import { getConfig } from '@/api'

const state = {
  list: [],
}

// getters
const getters = {
  // 任务相关
  taskVisible: (state) => {
    return state.list[2] ? state.list[2].enable === 1 : false
  },
  // 查看商品详情
  goodsVisible: (state) => {
    return state.list[0] ? state.list[0].enable === 1 : false
  },
  // 邀请新成员
  inviteVisible: (state) => {
    return state.list[1] ? state.list[1].enable === 1 : false
  },
}

// actions
const actions = {
  async getConfig ({commit}) {
    const result = await getConfig()
    if(result.success) {
      const { data } = result
      commit('setConfig', data)
    }
  },
}

// mutations
const mutations = {
  setConfig (state, config) {
    state.list = config
  },
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}