<template>
  <van-image :width="width" :height="height" :round="round" :lazy-load="lazyLoad" :fit="fit" :error-icon="errorImg" :src="src" />
</template>

<script>
import Vue from "vue";
import { Image, Lazyload } from "vant";

Vue.use(Image);
Vue.use(Lazyload);

export default {
  props: {
    width: {
      type: String || Number,
      default: 10
    },
    height: {
      type: String || Number,
      default: 10
    },
    round: {
      type: Boolean,
      default: false
    },
    lazyLoad: {
      type: Boolean,
      default: true
    },
    src: {
      type: String,
      default: ""
    },
    fit: {
      type: String,
      default: "cover"
    },
    errorImg: {
      type: String || Object,
      default: require('@/assets/default.png')
    },
  }
};
</script>