import Fly from 'flyio/dist/npm/fly'
import {
  Toast
} from 'vant'

/**
 * request.get({ // get, post, put, delete
 *  url: 'test', // api地址
 *  data: { test: 1 }, // 参数
 *  showLoading: true, // 是否触发请求加载状态，默认true
 *  showError: true // 是否触发请求出错提示，默认true
 * })
 */

// 允许多例
Toast.allowMultiple();

// loading实例
let toastLoadingArr = []

let toastId = 0

class Request {
  constructor() {

    this.fly = new Fly()

    //设置超时
    this.fly.config.timeout = 10000;

    //设置请求基地址
    this.fly.config.baseURL = process.env.VUE_APP_BASE_API

    //添加请求拦截器
    this.fly.interceptors.request.use((request) => {
      request.headers['access-token'] = localStorage.getItem('token') || ''
      toastId ++
      request.toastId = toastId
      const {
        showLoading
      } = request
      if (showLoading) {
        toastLoadingArr.push({
          toastId,
          loading: Toast.loading({
            duration: 0
          })
        })
      }
      return request;
    })

    //添加响应拦截器，响应拦截器会在then/catch处理之前执行
    this.fly.interceptors.response.use(
      (response) => {
        //成功响应会走到这里
        const {
          showError, toastId,
        } = response.request
        const index = toastLoadingArr.findIndex(item => item.toastId === toastId)
        if (index > -1) {
          toastLoadingArr[index].loading.clear()
          toastLoadingArr.splice(index, 1)
        }
        const data = {
          ...response.data,
          success: response.data.code === 1
        }
        checkSuccess(data, showError)
        return data
      },
      (err) => {
        //发生网络错误后会走到这里
        Toast.clear(true)
        checkHttpStatus(err)
      }
    )
  }
  _requestData(params, {
    method
  }) {
    const {
      url,
      data,
      showLoading = true,
      showError = true
    } = params
    return this.fly.request(url, data, {
      method,
      showLoading,
      showError,
    })
  }
  get(params) {
    return this._requestData(params, {
      method: 'GET'
    })
  }
  post(params) {
    return this._requestData(params, {
      method: 'POST'
    })
  }
  put(params) {
    return this._requestData(params, {
      method: 'PUT'
    })
  }
  delete(params) {
    return this._requestData(params, {
      method: 'DELETE'
    })
  }
}

let timer = null

// 请求成功处理
const checkSuccess = (data, showError) => {
  const code = data.code
  // 处理其他业务code
  if (code !== 1) {
    console.log('error：', data)
    if (showError) {
      Toast(data.msg)
    }
    if (code === -1) { // 重新登录
      clearTimeout(timer)
      Toast('登录过期，请重新登录');
      timer = setTimeout(() => {
        localStorage.clear()
        location.reload()
      }, 2000)
    }
  }
}

// 请求失败处理
const checkHttpStatus = (err) => {
  console.log('请求失败:', err)
  let message = ''
  switch (err.status) {
    case 0:
      message = '网络错误，请检查网络后重试'
      break
    case 1:
      message = '请求超时'
      break
    default:
      message = err.message || '请求出错'
  }
  Toast(message)
}

const request = new Request()

export default request