import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/home'
  },
  {
    path: '/login',
    component: () => import('@/views/login'),
    meta: { title: '登录' },
    children: [
      {
        path: '',
        name: 'wx',
        component: () => import('@/views/login/wx'),
        meta: { title: '登录' },
      },
      {
        path: 'account',
        name: 'account',
        component: () => import('@/views/login/account'),
        meta: { title: '登录' },
      },
      {
        path: 'verification',
        name: 'verification',
        component: () => import('@/views/login/verification'),
        meta: { title: '登录' },
      },
      {
        path: 'code',
        name: 'code',
        component: () => import('@/views/login/code'),
        meta: { title: '登录' },
      },
      {
        path: 'bind-mobile',
        name: 'bind-mobile',
        component: () => import('@/views/login/bind-mobile'),
        meta: { title: '绑定手机号' },
      }
    ]
  },
  {
    path: '/home',
    component: () => import('@/views/tab'),
    meta: { title: '数据分析' },
    children: [
      {
        path: '',
        name: 'data',
        component: () => import('@/views/tab/data'),
        meta: { title: '数据分析' },
      },
      {
        path: 'team',
        name: 'team',
        component: () => import('@/views/tab/team'),
        meta: { title: '团队' },
      },
      {
        path: 'market',
        name: 'market',
        component: () => import('@/views/tab/market'),
        meta: { title: '营销中心' },
      },
      {
        path: 'mine',
        name: 'mine',
        component: () => import('@/views/tab/mine'),
        meta: { title: '我的' },
      }
    ]
  },
  {
    path: '/goods',
    component: () => import('@/views/goods'),
    meta: { title: '商品销量分析' },
    children: [
      {
        path: '',
        name: 'goods-volume',
        component: () => import('@/views/goods/volume'),
        meta: { title: '商品销量分析' },
      },
      {
        path: 'detail',
        name: 'goods-detail',
        component: () => import('@/views/goods/detail'),
        meta: { title: '商品销售情况' },
      },
      {
        path: 'search',
        name: 'goods-search',
        component: () => import('@/views/goods/search'),
        meta: { title: '商品搜索' },
      },
    ]
  },
  {
    path: '/team',
    component: () => import('@/views/team'),
    meta: { title: '团队销售分析' },
    children: [
      {
        path: '',
        name: 'team-volume',
        component: () => import('@/views/team/volume'),
        meta: { title: '团队销售分析' },
      },
      {
        path: 'detail',
        name: 'team-detail',
        component: () => import('@/views/team/detail'),
        meta: { title: '团队销售情况' },
      },
      {
        path: 'info',
        name: 'team-info',
        component: () => import('@/views/team/info'),
        meta: { title: '基础资料' },
      },
    ]
  },
  {
    path: '/order',
    component: () => import('@/views/order'),
    meta: { title: '订单管理' },
    children: [
      {
        path: '',
        name: 'order-list',
        component: () => import('@/views/order/list'),
        meta: { title: '订单管理' },
      },
      {
        path: 'search',
        name: 'order-search',
        component: () => import('@/views/order/search'),
        meta: { title: '搜索订单' },
      },
      {
        path: 'logistics',
        name: 'order-logistics',
        component: () => import('@/views/order/logistics'),
        meta: { title: '查看物流' },
      }
    ]
  },
  {
    path: '/collect',
    name: 'collect',
    component: () => import('@/views/collect'),
    meta: { title: '销退货汇总' }
  },
  {
    path: '/pepole',
    name: 'pepole',
    component: () => import('@/views/pepole'),
    meta: { title: '新增人数' }
  },
  {
    path: '/task',
    component: () => import('@/views/task'),
    meta: { title: '待审核任务' },
    children: [
      {
        path: '',
        name: 'task-list',
        component: () => import('@/views/task/list'),
        meta: { title: '待审核任务' },
      },
      {
        path: 'detail',
        name: 'task-detail',
        component: () => import('@/views/task/detail'),
        meta: { title: '审核详情' },
      },
    ]
  },
  {
    path: '/404',
    name: '404',
    component: () => import('@/views/404'),
    meta: { title: '404' }
  },
  {
    path: '*',
    redirect: '/404'
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior: (to, from, savedPosition) => {
    if (savedPosition) {
      return savedPosition
    }
    return { x: 0, y: 0 }
  },
})

export default router
